                font-size: 16px;
                font-weight: 400;
                width: 950px;
                line-height: normal;
              "
              >تأسس هذا الكيان رسمياً عام 1404هـ كمؤسسة مقتصرة على مجموعة مغلقة
              ومحددة من المساهمين، مختصة بتقديم خدمات الحج لحجاج بيت الله الحرام
              القادمين حصراً من الدول الأفريقية غير العربية ، تماشياً مع رؤية
              المملكة 2030 م وتوجيهات القيادة الرشيدة- حفظها الله التي أطلقت
              برنامج خدمة ضيوف الرحمن والتي تهدف إلى استقبال (٣٠) مليون
              حاج.</v-card-text
            >
