<template>
  <div class="quick-img">
    <v-dialog v-model="dialog">
      <v-container>
        <v-row>
          <v-col cols="12" class="d-flex align-center justify-center">
            <div
              style="
                background-color: #fff;
                width: 50px;
                height: 50px;
                cursor: pointer;
                border-radius: 50%;
              "
              class="close-popup d-flex align-center justify-center"
              @click="dialog = false"
            >
              <span style="font-size: 35px; color: #a77646">x</span>
            </div>
          </v-col>
          <v-col cols="12">
            <div class="img text-center">
              <img src="@/assets/images/img-popupp.svg" alt="" />
            </div>
          </v-col>
          <v-col cols="12">
            <div class="icon py-8 d-flex align-center justify-space-between">
              <v-icon
                style="
                  font-size: 20px;
                  color: #a77646;
                  background-color: #fff;
                  border-radius: 50%;
                  padding: 25px;
                "
                icon="mdi-arrow-right"
              />
              <v-icon
                style="
                  font-size: 20px;
                  color: #a77646;
                  background-color: #fff;
                  border-radius: 50%;
                  padding: 25px;
                "
                icon="mdi-arrow-left"
              />
            </div>
          </v-col>
        </v-row>
      </v-container>
    </v-dialog>
  </div>
</template>
<script setup>
import { onMounted, ref, inject } from "vue";

const Emitter = inject(["Emitter"]);

// Data
const dialog = ref(false);

// Mounted
onMounted(() => {
  Emitter.on("openPopup", (data) => {
    dialog.value = data;
    dialog.value = true;
  });
});
</script>

