<template>
  <div class="quick-ved">
    <v-dialog v-model="dialog">
      <v-container>
        <v-row>
          <v-col cols="12" class="d-flex align-center justify-center">
            <div
              style="
                background-color: #fff;
                width: 50px;
                height: 50px;
                cursor: pointer;
                border-radius: 50%;
              "
              class="close-popup d-flex align-center justify-center"
              @click="dialog = false"
            >
              <span style="font-size: 35px; color: #a77646">x</span>
            </div>
          </v-col>
          <v-col cols="12">
            <div class="img text-center">
              <img src="@/assets/images/vedio-popup.svg" alt="" />
            </div>
          </v-col>
        </v-row>
      </v-container>
    </v-dialog>
  </div>
</template>
<script setup>
import { onMounted, ref, inject } from "vue";

const Emitter = inject(["Emitter"]);

// Data
const dialog = ref(false);

// Mounted
onMounted(() => {
  Emitter.on("openVedio", (data) => {
    dialog.value = data;
    dialog.value = true;
  });
});
</script>

<style lang="scss" scoped>
/* Media Quiery */
@media (max-width: 1280px) {
  .img {
    img {
      width: 100% !important;
    }
  }
}
</style>
